* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: fantasy;
}

.App {
  height: 150vh;
  width: 100%;
  background: linear-gradient(0.25turn, #C6FFDD, #FBD786, #f7797d);
  position: relative;

  display: grid;
  row-gap: 3em;
  grid-template-rows: 30vh 100vh;
}

  .newGame,
  .player1,
  .player2, 
  i {
   font-size: 2em ;
  }


.top-section {
  background-color: white;
  width: 75%;
  height: 100%;
  justify-self: center;
  margin-top: 1.5rem;
  //position: absolute;
  //left: 50%;
  //transform: translate(-50%, 1em);

  display: grid;
  grid-template-columns: 0 40% 10% 40% 0%;
  justify-items: center;
  align-content: center;
}

.top-middle {
  grid-column: 3 / span 1;

  display: grid;
  align-content: center;
}

.options {
  padding: 3em;
}

.newGame {
  outline: none;
  text-decoration: none;
  width: 6em;
  height: 4em;
  border-radius: 3px;
  background: wheat;
  transition: all .4s;
  font-size: 1.5em;
  border: 1px solid black;

  &:active {
    background: rgb(212, 226, 150);
  }
}

.left,
.right {
  display: grid;
  justify-items: center;
}

.player1,
.player2 {
  font-size: 3em;
  font-weight: 100;
  font-family: fantasy;

  display: grid;
  justify-items: center;
}

.left {
  grid-column: 2 / span 1;
}

.right {
  grid-column: 4 / span 1;
}

i {
  font-size: 5em;  
}

.bottom {
  grid-row: 2/3;
  display: grid;
  grid-template-rows: repeat(auto-fit, 12em);
  grid-template-columns: repeat(auto-fit, 8em);

  column-gap: 2em;
  row-gap: 2em;
  justify-content: center;
  
}

.card {
  width: 4em;
  position: relative;
  padding: 2em;

  display: grid;  

  &__front {
    position: absolute;
    width: 100%;
  }

  &__back {
    position: absolute;
    width: 100%;
    transition: all .4s;
    opacity: 1;
  }
}

.activePlayer {
  background: linear-gradient(0.25turn, #C6FFDD, #FBD786, #f7797d);
  opacity: .75;
}

@media (max-width: 900px) {
  .App {
    height: 200vh;
    grid-template-rows: 22vh 150vh;
  }
}

@media (max-width: 600px) {
  .App {
    height: 300vh;
    grid-template-rows: 22vh 250vh;
  }
}

@media (max-width: 500px) {
  .newGame,
  .left,
  .right {
   transform: scale(.75);
  }
}

@media (max-width: 400px) {
  .App {
    height: 500vh;
    grid-template-rows: 22vh 500vh;
  }

  .top-section {
    width: 100%;
  }
}